.header {
    padding: 17px 0;

    &--position {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 10;
    }

    &__container {
        align-items: center;
        justify-content: space-between;
    }
}

.nav {
    align-items: center;
    padding-right: 16px;

    &__list {
        align-items: center;
        margin-right: 32px;
        gap: 51px;

        @include small-tablet {
            margin-right: 0;
        }
    }

    &__link {
        color: var(--light-color);
        font-size: var(--main-size);
        transition: color 0.3s ease-out;

        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }
    }
}

