.approach {

    &__top {
        background-color: #F7F8F8;
        padding: 80px 0;
    }

    &__left {
        grid-column: 5 span;
        padding-top: 33px;

        @include big-desktop {
            margin-bottom: 32px;

        }

        @include small-tablet {
            grid-column: 4 span;
        }
    }

    &__right {
        grid-column: 7 / 6 span;
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;
        gap: 8px;

        @include big-desktop {
            grid-column: 12 span;
            justify-content: flex-start;
        }

        @include small-tablet {
            grid-column: 4 span;
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    &__title {
        margin: 0;
        margin-bottom: 13pxx;
        font-weight: 400;
        font-size: 22px;
        line-height: 22px;
        color: var(--dark-color);
    }

    &__descr {
        font-weight: 400;
        font-size: 15px;
        line-height: 24px;
        color: var(--text-color);
        margin-bottom: 37px;
    }

    &__btn {
        color: #00413E;
        border: 1px solid rgba(53, 83, 98, 0.2);
        padding: 16px 34px;

        @include small-tablet {}
    }

    &__bottom {
        padding: 24px 0;
    }

    &__item {
        grid-column: 2 span;
    }

    &__link {
        display: block;
        background-position: left top 16px;
        background-size: 24px 24px;
        background-repeat: no-repeat;
        padding-top: 17px;
        padding-left: 36px;
        border-top: 1px solid rgba(53, 83, 98, 0.3);
        transition: color 0.3s ease-out;
        font-weight: 400;
        font-size: 15px;
        line-height: 24px;

        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }

        &--ted {
            background-image: url("./../../img/approach-icon-1.svg");
        }

        &--nyt {
            background-image: url("./../../img/approach-icon-2.svg");
        }

        &--state {
            background-image: url("./../../img/approach-icon-3.svg");
        }

        &--ap {
            background-image: url("./../../img/approach-icon-4.svg");
        }

        &--time {
            background-image: url("./../../img/approach-icon-5.svg");
        }

        &--ft {
            background-image: url("./../../img/approach-icon-6.svg");
        }
    }
}