.information {
    background-color: #F4F5F5;
    padding-bottom: 140px;

    @include small-tablet {
        padding: 80px 0;
    }

    &__text {
        grid-column: 5 span;

        @include small-tablet {
            grid-column: 4 span;
        }
    }

    &__list {
        grid-column: 7 / 6 span;
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        column-gap: 48px;
        row-gap: 36px;
        list-style: none;

        padding: 0;

        @include small-tablet {
            grid-column: 4 span;
        }
    }

    &__item {
        grid-column: 3 span;
    }

    &__title {
        font-family: var(--second-family);
        margin: 0;
        font-weight: 400;
        font-size: 31px;
        line-height: 38px;
        letter-spacing: 0.04em;
        color: var(--dark-color);

        @include small-tablet {
            margin-bottom: 20px;
            font-size: 22px;
        }
    }

    &__subtitle {
        margin: 0;
        margin-bottom: 4;
        font-weight: 400;
        font-size: 15px;
        line-height: 24px;
        color: var(--dark-color);
    }

    &__descr {
        margin: 0;
        font-size: 15px;
        line-height: 24px;
        color: rgba(53, 83, 98, 0.8);
    }

}