.hero {
    display: flex;
    flex-direction: column;

    min-height: 776px;
    background-image: url('../../img/hero-bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 190px;
    padding-bottom: 40px;

    color: var(--light-color);

    @include mobile {
        padding-top: 150px;
    }

    &__container {
        margin-bottom: 48px;
    }

    &__content {
        grid-column: 6 span;

        @include small-tablet {
            grid-column: 4 span;
        }
    }

    &__title {
        margin: 0;
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 38px;
        line-height: 44px;
        letter-spacing: 1.2px;
        margin-bottom: 29px;

        @include tablet {
            font-size: 32px;
            line-height: 40px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: 29px;
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
    }

    &__bottom-container {
        margin-top: auto;
        width: 100%;
    }

    &__list {
        @include small-tablet {
            row-gap: 32px;
        }
    }

    &__subtitle,
    &__link {
        margin: 0;
        font-weight: 400;
        font-size: var(--second-size);
        line-height: 24px;
    }

    &__subtitle {
        margin-bottom: 15px;
    }

    &__item {
        grid-column: 2 span;
    }

    &__link {
        display: block;
        padding-top: 17px;
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        transition: color 0.3s ease-out;

        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }

        @include small-tablet {
            padding-top: 7px;
        }
    }
}