.press {
    padding-top: 87px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 33px;

        @include small-tablet {
            flex-wrap: wrap;
            gap: 20px;
        }
    }

    &__link {
        color: #00413E;
        border-color: rgba(53, 83, 98, 0.2);
    }

    &__title {
        font-family: var(--second-family);
        margin: 0;
        color: var(--dark-color);
        font-weight: 400;
        font-size: 27px;
        line-height: 38px;
        letter-spacing: 0.04em;
    }

    &__list {
        row-gap: 32px;
    }

    &__item {
        grid-column: 4 span;
    }
}

.press-card {
    position: relative;
    min-height: 145px;

    padding-top: 16px;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    ;

    &__top {
        display: flex;
        gap: 16px;
        margin-bottom: 11px;
        font-weight: 400;
        font-size: 11px;
        line-height: 16px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: rgba(53, 83, 98, 0.6);
    }

    &__title {
        margin: 0;
        font-weight: 400;
        font-size: 17px;
        line-height: 25px;
        color: #00413e;
    }

    &__author {
        display: block;
        background-position: left top 16px;
        background-size: 24px 24px;
        background-repeat: no-repeat;
        margin-top: -20px;
        margin-bottom: -4px;
        padding-top: 20px;
        padding-bottom: 4px;
        padding-left: 36px;

        &--ted {
            background-image: url("./../../img/approach-icon-1.svg");
        }

        &--cnews {
            background-image: url("./../../img/cnews.svg");
        }

        &--doj {
            background-image: url("./../../img/doj.svg");
        }

        &--nyt {
            background-image: url("./../../img/approach-icon-2.svg");
        }

        &--corr {
            background-image: url("./../../img/corr.svg");
        }

        &--roc {
            background-image: url("./../../img/roc.svg");
        }
    }

    &__link {
        transition: color 0.3s ease-out;

        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }
    }
}