.info {
    position: relative;
    background-color: #001414;
    height: 680px;
    display: flex;

    &::before {
        content: '';
        width: 287px;
        height: 100%;
        flex-shrink: 0;
        background-image: url("./../../img/info-1.png");

        @include tablet {
            display: none;
        }
    }

    &::after {
        content: '';
        width: 287px;
        height: 100%;
        flex-shrink: 0;
        background-image: url("./../../img/info-2.png");

        @include tablet {
            display: none;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;

        color: var(--light-color);
        padding: 0 82px;
        flex-grow: 1;
        border-left: 1px solid #001414;
        border-right: 1px solid #001414;

        @include tablet {
            padding: 60px;
        }
    }

    &__text {
        max-width: 542px;
    }

    &__title {
        margin: 0;
        font-weight: 400;
        font-size: 22px;
        line-height: 22px;
    }

    &__descr {
        margin: 0;
        font-size: 15px;
        line-height: 24px;
    }

    @include tablet {
        height: auto;
    }
}