.where {
    padding: 146px 0 133px 0;
    background-color: #001F1F;
    color: var(--light-color);

    @include small-tablet {
        padding: 80px 0;
    }

    &__content {
        grid-column: 4 span;

        @include small-tablet {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        margin: 0;
        margin-bottom: 43px;
        font-size: 36px;
        line-height: 52px;
        letter-spacing: 0.03em;
        color: #fafafa;

        @include small-tablet {
            font-size: 22px;
            margin-bottom: 24px;
        }
    }

    &__descr {
        margin: 0;
        margin-bottom: 37px;
        max-width: 320px;
        font-size: 18px;
        line-height: 28px;
        color: #fafafa;

        @include small-tablet {
            text-align: center;
            font-size: 16px;
            margin-bottom: 24px;
        }
    }

    &__link {
        color: var(--light-color);
        background-color: #00665f;
        border-color: #00665f;
    }

    &__map {
        grid-column: 7 / 5 span;


        @include small-tablet {
            grid-column: 4 span;
            margin: 0 auto;
            margin-top: 32px;
        }
    }
}