.partners {
    padding: 17px 0;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include big-desktop {
            justify-content: space-around;
        }

        @include tablet {
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    &__img {
        @include big-desktop {
            max-width: 130px;
        }
    }

    &__title,
    &__more {
        margin: 0;
        font-weight: 400;
        line-height: 16px;
        font-size: 11px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: rgba(53, 83, 98, 0.6);
    }

    &__title {
        @include tablet {
            order: 1;
            margin-right: 8px;
            margin-bottom: 8px;
        }
    }

    &__more {
        @include tablet {
            order: 2;
            margin-left: 8px;
            margin-bottom: 8px;
        }
    }

    &__list {
        display: flex;
        gap: 13px;
        align-items: center;
        margin: 0 6px;

        @include big-desktop {
            justify-content: space-between;
        }

        @include tablet {
            flex-wrap: wrap;
            justify-content: center;
            order: 3;
        }
    }

    &__more {
        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }
    }
}