.steps {
    padding-top: 120px;
    padding-bottom: 120px;

    @include small-tablet {
        padding: 80px 0;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        max-width: 720px;
        margin: 0 auto;
        margin-bottom: 57px;
        text-align: center;
        font-size: 30px;
        line-height: 38px;
        letter-spacing: 1.2px;

        @include small-tablet {
            font-size: 24px;
            line-height: 32px;
        }
    }

    &__list {
        counter-reset: steps;
        justify-content: space-between;
        max-width: 758px;
        margin: 0 auto;
        list-style: none;

        @include small-tablet {
            flex-wrap: wrap;
            gap: 32px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;

        &::before {
            counter-increment: steps;
            content: counter(steps);

            display: flex;
            align-items: center;
            justify-content: center;
            width: 48px;
            height: 48px;
            background-color: #014C48;
            border-radius: 100%;
            margin-bottom: 20px;
            color: var(--light-color);

            font-size: 16px;
            line-height: 30px;
        }

        &--line {
            flex-grow: 1;
            position: relative;

            &::before {
                display: none;
            }

            &::after {
                content: '';
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                margin-top: -35px;
                width: 100%;
                height: 1px;
                background-color: rgba(53, 83, 98, 0.2);

            }
        }

        @include small-tablet {
            width: 100%;

            &::after {
                display: none;
            }
        }
    }

    &__descr {
        margin: 0;
        max-width: 126px;
        font-weight: 400;
        font-size: 15px;
        line-height: 24px;
        text-align: center;
        color: rgba(53, 83, 98, 0.8);

        @include small-tablet {
            max-width: 180px;
        }
    }
}