.footer {

    &__top {
        padding: 112px 0 137px;
        background-color: #001414;

        .container {
            @include small-tablet {
                row-gap: 32px;
            }
        }

        @include small-tablet {
            padding: 60px 0;
        }
    }

    &__column {
        grid-column: 2 span;

        &:last-child {
            grid-column: 10 / 2 span;

            @include small-tablet {
                grid-column: 2 span;
            }
        }
    }

    &__title {
        position: relative;
        margin: 0;
        padding-bottom: 24px;
        margin-bottom: 23px;
        font-weight: 400;
        font-size: 11px;
        line-height: 16px;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        color: #7d9798;

        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 16px;
            height: 1px;
            background-color: #25B895;
        }
    }

    &__list {
        color: #EDECEB;
        font-size: 15px;
        line-height: 30px;
    }

    &__link {
        transition: color .3s ease-out;

        @include hover {
            color: var(--accent-color);
        }

        &:active {
            color: var(--active-color);
        }
    }

    &__bottom {
        padding: 40px 0 63px;
        background-color: #000F0F;

        &-container {
            display: flex;
            justify-content: space-between;
            align-items: center;

            @include small-tablet {
                flex-direction: column;
                gap: 20px;
            }
        }

        @include small-tablet {
            padding: 30px 0;
        }
    }

    &__text {
        color: #E5ECEC;
        font-size: 10px;

        @include small-tablet {
            text-align: center;
        }
    }

    &__sublist {
        display: flex;
        gap: 8px;
        margin-bottom: 8px;
        padding: 0;
        align-items: center;
        list-style: none;

        &-link {
            transition: color .3s ease-out;

            @include hover {
                color: var(--accent-color);
            }

            &:active {
                color: var(--active-color);
            }
        }

        @include small-tablet {
            display: flex;
            justify-content: center;
        }
    }

    &__copy-inner {
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
}